import React from "react"
import { Helmet } from "react-helmet";
import { Link } from "react-router-dom";
import headerBackground from 'assets/images/banner.jpg';

export default class WebViewer extends React.Component {
    constructor() {
        super();
        this.state = { playButtonVisibility: 'visible' };
    }

    render() {
        return (
            <div className='homepage center center-page-x'>
                <Helmet>
                    <meta charSet="utf-8" />
                    <title>Interactive online 3D viewer with AR and VR technologies</title>
                    <link rel="canonical" href="https://www.ezyvue.com/platform/webviewer" />
                </Helmet>
                <section className="full-width-header" 
                    style={{ 
                        backgroundImage: `url(${headerBackground})`, 
                        justifyContent: 'left', 
                        backgroundPosition: '80%'}
                    }>
                    <div style={{
                        maxWidth: '600px',
                        marginLeft: '5  %'
                    }}>
                        <h1>
                           Get ahead of the competition with the Ezyvue 3D viewer
                        </h1>
                        <h3>
                            With the interactive 3D Viewer you can showcase products in a more interactive and engaging way
                        </h3>
                      {/*   <span style={{ color: '#FF9913' }}>Plans from $0 per month</span> */}
                    </div>
                    <div className='full-width-header-promo-button'>
                        <div className="homepage-button-medium">
                            <Link to='/pricing'>Get started for free</Link>
                        </div>
                    </div>
                </section>
                <div className='homepage-section'>
                    <h1>Interactive online 3D viewer with AR and VR technologies</h1>
                    <h4>
                        The Ezyvue 3D Viewer provides a realistic and interactive shopping experience for your customers   
                    </h4>
                    <div className='aspect-four-three' style={{ width: '100%', maxWidth: '600px', margin: 'auto' }}>
                        <div className='aspect-inner'>
                            <iframe id="ezyvue-viewer"
                                src={`${process.env.REACT_APP_VIEWER_URL}/viewer?productId=62bd1495fa896f920473a4e8&playbutton&animateplaybutton&hidemenu&showbackground=true`}
                                width="100%"
                                height="100%"
                                style={{ border: 'none' }}
                                frameBorder="0"
                                allow="xr-spatial-tracking"
                                allowFullScreen={true}
                                mozallowfullscreen="true"
                                webkitallowfullscreen="true" />
                        </div>
                    </div>
                </div>
                <div className="product-features-row">
                    <div className="product-features-box">
                        <div>
                            <h3>View a model interactively in 3D from any angle</h3>
                            <p>
                                Easy to use 360&deg; camera rotation, panning and zooming. <br />
                                First-person camera option for walking through a scene.<br/>
                                Automatic camera tracking to specific features with annotations.
                            </p>
                        </div>
                        <div>
                            <img className='homepage-section-img' style={{ borderRadius: '10px' }}
                                alt='3d product visualization from any angle'
                                src={require('../../assets/images/camera.jpg')} loading='lazy' />
                        </div>
                    </div>
                    <div className="product-features-box">
                        <div>
                            <h3>High quality HD rendering</h3>
                            <p>
                                The ThreeJS web rendering technology brings your products to life with realistic shadows, reflections, PBR materials, HDR lighting and post-processing effects.
                            </p>
                        </div>
                        <div>
                            <img className='homepage-section-img'
                                alt='reaslistic 3d product rendering'
                                src={require('../../assets/images/toaster.jpg')} loading='lazy' />
                        </div>
                    </div>
                </div>
                <div className="product-features-row">
                    <div className="product-features-box">
                        <div>
                            <h3>AR and VR</h3>
                            <p>
                                Enhance your website with immersive AR and VR experiences.<br />
                                AR helps to increase sales and reduce returns by making customers more confident that a product is the right choice for their home or workplace.<br />
                            </p>
                            <Link to='/platform/augmentedreality'>Find out more</Link>
                        </div>
                        <div>
                            <img alt='augmented reality for ios and android'
                                src={require('../../assets/images/ar.jpg')} loading='lazy' />
                        </div>
                    </div>
                    <div className="product-features-box">
                        <div>
                            <h3>Personalized buying experience for your customers</h3>
                            <p>
                                Build your own product configurator and let your customers choose from all the options such as color, texture and component parts.<br />
                                Shoppers can personalize products in their own home with the WebAR viewer on compatible browsers. </p>
                        </div>
                        <div>
                            <img className='homepage-section-img' alt='sofa product configurator'
                                src={require('../../assets/images/tshirtvariants.jpg')} loading='lazy' />
                        </div>
                    </div>
                </div>
                <div className="product-features-row">
                    <div className="product-features-box">
                        <div>
                            <h3>Demonstrate moving parts with animations</h3>
                            <p>
                                Make your website more interactive and demonstrate features, mechanisms and moving parts with animations.<br/>
                                Start animations interactively when a user clicks an animation button attached to a model.
                            </p>
                        </div>
                        <div>
                            <img style={{ opacity: '90%' }} className='homepage-section-img' alt='3d animations'
                                src={require('../../assets/images/dishwasher.jpg')} loading='lazy' />
                        </div>
                    </div>
                    <div className="product-features-box">
                        <div>
                            <h3>Annotate your models</h3>
                            <p>
                                Highlight key features with interactive 3d text annotations. Click an annotation button to quickly zoom and track the camera to the point of interest and display text.
                            </p>
                        </div>
                        <div>
                            <img className='homepage-section-img' alt='3d annotations'
                                src={require('../../assets/images/toaster-detail.jpg')} loading='lazy' />
                        </div>
                    </div>
                </div>
                <div className="product-features-row">
                    <div className="product-features-box">
                        <div>
                            <h3>Ezyvue Studio</h3>
                            <p>
                                Compose your project scenes, edit lighting, cameras, shadows, materials and textures. Add 3D text annotations, create a product configurator and more.<br/>
                            </p>
                        </div>
                        <div>
                            <Link to='/modelViewer?productId=610f4cb5a4a56065eceb712e'>
                                <img className='homepage-section-img' alt='catalog'
                                    src={require('../../assets/images/computer.jpg')} loading='lazy' />
                            </Link>
                        </div>
                    </div>
                    <div className="product-features-box">
                        <div>
                            <h3>Product demonstration</h3>
                            <p>
                                In demonstration mode the viewer is ideal for showing a product range to clients in 3d and AR.
                            </p>
                        </div>
                        <div>
                            <Link to={`/modelViewer?productId=610f4cb5a4a56065eceb712e`}>
                                <img className='homepage-section-img' alt='catalog'
                                    src={require('../../assets/images/catalog.jpg')} loading='lazy' />
                            </Link>
                        </div>
                    </div>
                </div>
                <div className="product-features-row">
                    <div className="product-features-box">
                        <div>
                            <h3>Dimensions Tool</h3>
                            <p>
                                Turn on the tool to show the dimensions of a 3d model.
                            </p>
                        </div>
                        <div>
                            <img className='homepage-section-img' alt='product dimensions'
                                src={require('../../assets/images/measurements.jpg')} loading='lazy' />
                        </div>
                    </div>
                    <div className="product-features-box">
                        <div>
                            <h3>
                                Easy website integration and support for most site builders
                            </h3>
                            <p>
                                The viewer can be integrated with your website with a single iframe HTML tag.
                                Follow our step-by-step guides to integrate the viewer with popular website builders.
                            </p>
                        </div>
                        <div>
                            <div className="flexrow" style={{ justifyContent: 'center' }}>
                                <div>
                                    <a href='https://www.wix.com' target='_blank' rel='noopener noreferrer'>
                                        <img alt='integrate with wix' src={require('../../assets/images/wix.jpg')} style={{ opacity: '0.4', display: 'inline', width: '150px' }} loading='lazy' />
                                    </a>
                                    <a href='https://www.webflow.com' target='_blank' rel='noopener noreferrer'>
                                        <img alt='integrate with webflow' src={require('../../assets/images/webflow.jpg')} style={{ opacity: '0.4', display: 'inline', width: '150px' }} loading='lazy' />
                                    </a>
                                    <a href='https://www.squarespace.com' target='_blank' rel='noopener noreferrer'>
                                        <img alt='integrate with squarespace' src={require('../../assets/images/squarespace.jpg')} style={{ opacity: '0.4', display: 'inline', width: '150px' }} loading='lazy' />
                                    </a>
                                    <a href='https://www.shopify.com' target='_blank' rel='noopener noreferrer'>
                                        <img alt='integrate with shopify' src={require('../../assets/images/shopify.jpg')} style={{ opacity: '0.4', display: 'inline', width: '150px' }} loading='lazy' />
                                    </a>
                                    <a href='https://www.wordpress.com' target='_blank' rel='noopener noreferrer'>
                                        <img alt='integrate with wordpress' src={require('../../assets/images/wordpress.jpg')} style={{ opacity: '0.4', display: 'inline', width: '150px' }} loading='lazy' />
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <br/>
                <br/>
                <br/>
            </div >

        );
    }
}